body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

<style type="text/css">
    .hidden { display:none; }
</style>


.choose:after {
  clear: both;
  content: "";
  display: table;
}  

.choose {
  /* flex-direction: row; */
  height: 65px;
  font-size: 38px;
  font-weight: bold;
  border: 1px solid #999;
  /* width: 115px; */
  /* margin-right: -1px; */
  /* padding: 5px; */
}

.choose:focus {
  outline: none;
}

.kbd-navigation .choose:focus {
  background: #ddd;
}

.chooseBox {
  /* display: flex; */
  /* flex-direction: row; */
  display: grid;
  grid-template-columns: 130px 180px 120px 142px;
  align-items: stretch;
  width: 580px;
}
  
.board-row {
  clear: both;
  content: "";
  display: grid;
  /* grid: auto / auto auto; */
  grid-template-columns: 290px 280px;


}

.status {
  margin-bottom: 10px;
}

.name {
  width: 249px;
  /* flex-grow: 25; */
}

.size {
  width: 125px;
  font-weight: normal;
  font-size: 16px;
  /* flex-grow: 12; */
}

.price {
  width: 125px;
  font-weight: normal;
  font-size: 16px;
  /* flex-grow: 12; */
}

