.game {
  display: flex;
  flex-direction: column;
  width: 580px;
  
}

.game-info {
  display: flex;
  flex-direction: row;
  font-size: 24px;
  font-weight: bold;
  border: 1px solid #999;
  width: 560px;
  margin-right: -1px;
  padding: 5px;
  /* position: fixed; */
    position: sticky;

  background-color: lightblue;
  bottom: 0;
  left: 0;
}

.info-left {
  display: flex;
  flex-grow: 3;
  flex-direction: column;
}

.info-right {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-content: center;
 }


